* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

.reasons__id {
  scroll-margin-top: 450px; }

.main__container {
  background-color: #eff9ff;
  width: 320px;
  height: 624px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr; }

.main__tittle {
  align-self: center;
  grid-row: 1/2;
  grid-column: 1/3; }

.reasons {
  width: 290px;
  height: 30px;
  margin: 30px 15px 0 15px;
  font-family: 'roboto', sans-serif;
  font-size: 24px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.25;
  letter-spacing: normal;
  text-align: center;
  text-transform: capitalize;
  color: black; }

.main__textone {
  grid-row: 2/3;
  grid-column: 1/3; }

.main__textone--h3 {
  width: 184px;
  height: 30px;
  margin: 0 68px 0 68px;
  font-family: 'roboto slab', sans-serif;
  font-size: 18px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.67;
  letter-spacing: normal;
  text-align: center;
  color: black; }

.main__textone--h3::first-letter {
  text-transform: uppercase; }

.main__textone--p {
  width: 180px;
  height: 63px;
  margin: 0 70px 0;
  font-family: 'roboto', sans-serif;
  font-size: 18px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.67;
  letter-spacing: normal;
  text-align: center;
  color: black; }

.main__textone--p::first-letter {
  text-transform: uppercase; }

.main__texttwo {
  grid-row: 3/4;
  grid-column: 1/3; }

.main__textwo--h3 {
  width: 184px;
  height: 30px;
  margin: 0 68px 0 68px;
  font-family: 'roboto slab', sans-serif;
  font-size: 18px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.67;
  letter-spacing: normal;
  text-align: center;
  color: black; }

.main__textwo--h3::first-letter {
  text-transform: uppercase; }

.main__textwo--p {
  width: 180px;
  height: 63px;
  margin: 0 70px 0;
  font-family: 'roboto', sans-serif;
  font-size: 18px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.67;
  letter-spacing: normal;
  text-align: center;
  color: black; }

.main__textwo--p::first-letter {
  text-transform: uppercase; }

.main__texttree {
  grid-row: 4/5;
  grid-column: 1/3; }

.main__texttree--h3 {
  width: 184px;
  height: 30px;
  margin: 0 68px 0 68px;
  font-family: 'roboto slab', sans-serif;
  font-size: 18px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.67;
  letter-spacing: normal;
  text-align: center;
  color: black; }

.main__texttree--h3::first-letter {
  text-transform: uppercase; }

.main__texttree--p {
  width: 180px;
  height: 63px;
  margin: 0 70px 0;
  font-family: 'roboto', sans-serif;
  font-size: 18px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.67;
  letter-spacing: normal;
  text-align: center;
  color: black; }

