  text-transform: uppercase; }

.main__button {
  grid-row: 5/6;
  grid-column: 1/3;
  width: 184px;
  height: 45px;
  margin: 45px 65px 0;
  padding: 0 29.5px 0 30.5px;
  border-radius: 22.5px;
  background-color: #f26002;
  transition: 2s; }

.main__button:hover {
  background-color: cadetblue;
  border: black;
  color: blue; }

.main__button--text {
  width: 124px;
  height: 45px;
  font-family: 'roboto', sans-serif;
  font-size: 20px;
  /*font-weight: 500;*/
  font-stretch: normal;
  font-style: normal;
  line-height: 2.25;
  letter-spacing: normal;
  text-align: center;
  background-color: #f26002;
  color: white;
  border: none;
  transition: 2s; }

.main__button--text:hover {
  background-color: cyan;
  border: black;
  color: blue; }

